import React from 'react';
import { Card, Image, Modal, Header } from 'semantic-ui-react';

function ExperienceCard({ title, imgURL, header, description }) {
	return (
		<Modal
			size="small"
			trigger={
				<Card fluid>
					<Image src={imgURL} />
					<Card.Content>
						<Card.Header>{title}</Card.Header>
					</Card.Content>
				</Card>
			}
		>
			<Modal.Header>{title}</Modal.Header>
			<Modal.Content image>
				<Image size="medium" src={imgURL} />
				<Modal.Description>
					<Header>{header}</Header>
					<p>{description}</p>
				</Modal.Description>
			</Modal.Content>
		</Modal>
	);
}

export default ExperienceCard;
