import React from 'react';
import { Card, Image, Modal, Header, Button } from 'semantic-ui-react';

function ProjectCard({ title, tech, header, imgURL, webURL, github, showLinks, description }) {
	return (
		<Modal
			size="small"
			trigger={
				<Card fluid>
					<Image src={imgURL} />
					<Card.Content>
						<Card.Header>
							{title}
							{showLinks && (
								<Button
									basic
									circular
									icon="github"
									floated="right"
									onClick={() => {
										window.open(github, '_blank');
									}}
								/>
							)}
							{showLinks && (
								<Button
									basic
									circular
									icon="globe"
									floated="right"
									onClick={() => {
										window.open(webURL, '_blank');
									}}
								/>
							)}
						</Card.Header>
						<Card.Description>{tech}</Card.Description>
					</Card.Content>
				</Card>
			}
		>
			<Modal.Header>{title}</Modal.Header>
			<Modal.Content image>
				<Image size="medium" src={imgURL} />
				<Modal.Description>
					<Header>{header}</Header>
					<p>{description}</p>
					<p>Built with: {tech}</p>
					{showLinks && (
						<Button
							basic
							circular
							icon="globe"
							floated="left"
							onClick={() => {
								window.open(webURL, '_blank');
							}}
						/>
					)}

					{showLinks && (
						<Button
							basic
							circular
							icon="github"
							floated="left"
							onClick={() => {
								window.open(github, '_blank');
							}}
						/>
					)}
				</Modal.Description>
			</Modal.Content>
		</Modal>
	);
}

export default ProjectCard;
