import React from 'react';
import { Card, Image, Modal, Header, Button } from 'semantic-ui-react';

function SimpleCard({ title, header, description, description2, imgURL, webURL, showLink }) {
	return (
		<Modal
			size="small"
			trigger={
				<Card fluid>
					<Card.Content>
						<Image floated="right" size="tiny" src={imgURL} />
						<Card.Header>{title}</Card.Header>
					</Card.Content>
				</Card>
			}
		>
			<Modal.Header>
				{title}
				{showLink && (
					<Button
						basic
						circular
						icon="globe"
						floated="right"
						onClick={() => {
							window.open(webURL, '_blank');
						}}
					/>
				)}
			</Modal.Header>
			<Modal.Content image>
				<Image wrapped size="medium" src={imgURL} />
				<Modal.Description>
					<Header>{header}</Header>
					<p>{description}</p>
					<p>{description2}</p>
				</Modal.Description>
			</Modal.Content>
		</Modal>
	);
}

export default SimpleCard;
